		h1.display-2.my-2 {{ msg }}
		h2.display-1 Sobre Ns Feijo e Arroz
		h3.headline.mt-2 Trs Camundongos Cegos
		p.mt-3
			v-btn.blue-grey.white--text.with.dignity(large to="/x-file" exact)
				v-icon.mr-3 all_inclusive
				| De Trinitatibus


	//- .hello
	//- 	v-container(grid-list-xl text-xs-center)
	//- 		v-layout(row wrap)
	//- 			v-flex(xs12 align-center justify-center)
	//- 				h1.display-1.my-2 Sobre Pobres e Nobres
	//- 				h3.display-2 O Superusurio  Sambojano
	//- 				h3.headline.mt-2 Ao Vivo da Vila Beatriz
	//- 				img.my-3(src='../assets/hammer-sickle.svg', width='125px')
	//- 				p.my-2
	//- 					v-btn.blue-grey.white--text.with.dignity.mt-2(large to="/sobre-isso")
	//- 						v-icon group
	//- 						| &nbsp; Pobres &amp; Nobres

	v-container(fluid grid-list-md)
		v-layout(row wrap)

			v-flex(d-flex xs12 sm6 md4)
				v-card.pa-3(color='blue-grey', dark)
					v-card-title.title.with.dignity(primary)
						i.fa.fa-archway.fa-lg.mr-2
						| Lorem

					v-card-text {{ lorem }}

			v-flex(d-flex xs12 sm6 md3)
				v-layout(row wrap)
					v-flex(d-flex)
						v-card.pa-3(color='grey lighten-2' light)
							v-card-title.title.with.dignity(primary)
								i.fa.fa-archway.fa-lg.mr-2
								| Forum
							v-card-text {{ lorem.slice(0, 70) }}

					v-flex(d-flex)
						v-layout(row wrap)
							v-flex(v-for='n in 2', :key='n', d-flex xs12)
								v-card.pa-3(color='red lighten-2', dark)
									v-card-title.title.with.dignity(primary)
										i.fa.fa-archway.fa-lg.mr-2
										| Quorum
									v-card-text {{ lorem.slice(0, 40) }}

			v-flex(d-flex xs12 sm6 md2 child-flex)
				v-card.pa-3(color='yellow lighten-4' light)
					v-card-title.title.with.dignity(primary)
						i.fa.fa-archway.fa-lg.mr-2
						| Procul
					v-card-text {{ lorem.slice(0, 90) }}

			v-flex(d-flex xs12 sm6 md3)
				v-card.pa-3(color='black' dark)
					v-card-title.title.with.dignity(primary)
						i.fa.fa-archway.fa-lg.mr-2
						| Harum
					v-card-text {{ lorem.slice(0, 100) }}
</template>


<script>
	export default {
		data: () => ({
			lorem: `   c .   c c q, v   q .  q q  v. Q    ,    c c. q c  v,    cq , c  q v .`
		})
	}
</script>

<style scoped>

#bg {
position: fixed; 
top: -50%; 
left: -50%; 
width: 200%; 
height: 200%;
}

#bg img {
position: absolute; 
top: 0; 
left: 0; 
right: 0; 
bottom: 0; 
margin: auto; 
min-width: 50%;
min-height: 50%;
filter: grayscale(100%) contrast(70%) opacity(120%);
}


#bg {
	filter: grayscale(100%) contrast(35%);
}

h1, h2 {
	font-weight: normal;
}
ul {
	list-style-type: none;
	padding: 0;
}
li {
	display: inline-block;
	margin: 0 10px;
}
a {
	color: #42b983;
}

v-card {
	font-family: 'News Cycle', sans-serif;
	font-weight: 300;
}
</style>



